function giftSwitcher() {
  const switchers = document.querySelectorAll(".form__switcher-input");
  const gifts = document.querySelector(".form__gifts");
  if (gifts) {
    switchers.forEach(el => {
      el.addEventListener("click", toggleGifts);
    });
  }

  function toggleGifts(e) {
    if (e.target.checked && e.target.value === "gift") {
      gifts.style.height = gifts.scrollHeight + "px";
    } else {
      gifts.style.height = gifts.scrollHeight + "px";
      setTimeout(() => {
        gifts.style.height = 0;
      }, 25);
      setTimeout(() => {
        gifts.removeAttribute(style);
      }, 250);
    }
  }
}

giftSwitcher();

function validation() {
  let telInputs = document.querySelectorAll('input[type="tel"]');
  if (telInputs) {
    telInputs.forEach(tel => {
      Inputmask("+" + "999" + " (" + "99" + ")" + " " + "999" + "-" + "99" + "-" + "99", {
        greedy: false,
        placeholder: "*",
        validator: "[0-9]",
        onincomplete: function () {
          tel.setCustomValidity("Feel this field, please");
        },
        oncomplete: function () {
          tel.setCustomValidity("");
        },
        onBeforePaste: function (pastedValue, opts) {
          let normalizePastedValue = pastedValue.replace(/[^.\d]/g, "");
          if (normalizePastedValue.includes("380")) {
            normalizePastedValue = normalizePastedValue.slice(3);
          }
          if (normalizePastedValue.length >= 10) {
            normalizePastedValue = normalizePastedValue.slice(-9);
            tel.setCustomValidity("");
          }
          return normalizePastedValue;
        },
        autoUnmask: true,
      }).mask(tel);
    });
  }
}

validation();
